.gallery {
  overflow: scroll;
  margin-bottom: 30px;
  border-bottom: 1px solid var(--border-color);
  padding: 0;
  padding-bottom: 30px;

  &__list {
    gap: 30px;
  }

  &__item {
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    max-width: 360px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    padding: 20px 20px 20px 0;
  }

  &__counter {
    margin-bottom: 15px;
  }

  &__descr {
    font-family: var(--second-family);
    font-size: 45px;
    line-height: 1;
    text-transform: uppercase;
  }
}
