.built {
  position: relative;
  margin-bottom: 150px;
  padding: 0;
  background-image: url("../img/svg/built-bg.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__decoration {
    position: absolute;
    inset: 0;
    z-index: 1;
    pointer-events: none;
  }

  &__img--01 {
    position: absolute;
    right: 5%;
    top: -5%;
    z-index: 10;
    transform: rotate(-15deg);
  }

  &__img--02 {
    position: absolute;
    right: 20%;
    bottom: 30%;
    z-index: 10;
  }

  &__img--03 {
    position: absolute;
    left: 0;
    bottom: 30%;
    z-index: 10;
  }

  &__list {
    justify-content: space-between;
    width: 100%;
  }

  &__label {
    font-size: 14px;
    text-transform: uppercase;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-family: var(--second-family);
    font-size: 23.8vw;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
  }

  &__cards {
    justify-content: space-between;
  }

  &__card {
    max-width: 425px;
  }

  &__counter {
    display: block;
    margin: 0;
    margin-bottom: 15px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 15px;
    font-family: var(--second-family);
    font-size: 45px;
    line-height: 1;
    text-transform: uppercase;
  }
}
