.media {
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 60px;

  &__title {
    margin: 0;
    margin-bottom: 60px;
    font-family: var(--second-family);
    font-size: 13.2vw;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
  }

  &__social {
    display: flex;
    align-items: center;
    margin: 0 auto;
    margin-bottom: 60px;
    border-bottom: 1px solid var(--border-color);
    padding-bottom: 10px;
    max-width: fit-content;
    text-transform: uppercase;
  }

  &__icon {
    margin-left: 15px;
    width: 16px;
    height: 16px;
  }

  &__list {
    justify-content: space-between;
  }

  &__item {
    max-width: 405px;
  }

  &__link {
    flex-direction: column;
  }

  &__img {
    margin-bottom: 20px;
  }

  &__date {
    margin-bottom: 10px;
  }

  &__subtitle {
    margin: 0;
    font-family: var(--second-family);
    font-size: 32px;
    line-height: 1;
    text-transform: uppercase;
  }
}
